<template>
  <div>
    <Header></Header>
    <div id="map"></div>
  </div>
</template>

<script>
import Header from '../components/Header'
import AMap from 'AMap'
import AMapUI from 'AMapUI'
export default {
  name: 'District',
  components: {
    Header,
  },
  data() {
    return {
      map: null,
    }
  },
  created() {},
  mounted() {
    this.map = new AMap.Map('map', {
      zoom: 10,
      center: [121.62, 38.92],
      mapStyle: 'amap://styles/whitesmoke',
    })
    let that = this
    AMapUI.loadUI(['geo/DistrictExplorer'], function (DistrictExplorer) {
      //启动页面
      that.initPage(DistrictExplorer)
    })
  },
  methods: {
    renderAreaNode(districtExplorer, areaNode) {
      let that = this
      //清除已有的绘制内容
      districtExplorer.clearFeaturePolygons()
      //just some colors
      var colors = [
        '#3366cc',
        '#dc3912',
        '#ff9900',
        '#109618',
        '#990099',
        '#0099c6',
        '#dd4477',
        '#66aa00',
      ]
      //绘制子级区划
      districtExplorer.renderSubFeatures(areaNode, function (feature, i) {
        var fillColor = colors[i % colors.length]
        var strokeColor = colors[colors.length - 1 - (i % colors.length)]
        return {
          cursor: 'default',
          bubble: true,
          strokeColor: strokeColor, //线颜色
          strokeOpacity: 1, //线透明度
          strokeWeight: 1, //线宽
          fillColor: fillColor, //填充色
          fillOpacity: 0.35, //填充透明度
        }
      })
      //绘制父级区划，仅用黑色描边
      districtExplorer.renderParentFeature(areaNode, {
        cursor: 'default',
        bubble: true,
        strokeColor: '#333', //线颜色
        fillColor: null,
        strokeWeight: 2, //线宽
      })
      //更新地图视野以适合区划面
      that.map.setFitView(districtExplorer.getAllFeaturePolygons())
    },
    initPage(DistrictExplorer) {
      let that = this
      //创建一个实例
      var districtExplorer = new DistrictExplorer({
        eventSupport: true,
        map: that.map, //关联的地图实例
      })

      var adcode = 210200 //大连的区划编码

      //创建一个辅助Marker，提示鼠标内容
      var tipMarker = new AMap.Marker({
        //启用冒泡，否则click事件会被marker自己拦截
        bubble: true,
      })

      //监听feature的hover事件
      districtExplorer.on('featureMouseout featureMouseover', function (
        e,
        feature
      ) {
        var isHover = e.type === 'featureMouseover'

        if (!isHover) {
          tipMarker.setMap(null)
          return
        }

        tipMarker.setMap(that.map)

        tipMarker.setPosition(e.originalEvent.lnglat)

        tipMarker.setLabel({
          offset: new AMap.Pixel(20, 20),
          content: feature.properties.name,
        })
      })

      //监听鼠标在feature上滑动
      districtExplorer.on('featureMousemove', function (e, feature) {
        //更新提示位置
        console.log(feature)
        tipMarker.setPosition(e.originalEvent.lnglat)
      })

      //feature被点击
      districtExplorer.on('featureClick', function (e, feature) {
        console.log('点击: ' + feature.properties.name)
      })

      //外部区域被点击
      districtExplorer.on('outsideClick', function (e) {
        console.log('区域外点击', e)
      })
      districtExplorer.loadAreaNode(adcode, function (error, areaNode) {
        if (error) {
          console.error(error)
          return
        }
        //绘制载入的区划节点
        that.renderAreaNode(districtExplorer, areaNode)
      })
    },
  },
}
</script>

<style scoped>
#map {
  min-height: 95vh;
}
</style>